.course_editor{}
.course_editor .course_editor_c1{ width:320px; border:#ddd 1px solid; float:left; margin-right:30px}
.course_info_preview{ padding:15px 0}
.course_info_preview .info_cover{ position:relative; width:290px; margin:0 auto; height:160px; overflow:hidden; background:#ececec}
.course_info_preview .placetxt{ font-style:normal; color:#c0c0c0; font-size:14px}
.course_info_preview .info_cover .placetxt{ display:block; line-height:160px; font-family:Microsoft Yahei; text-align:center;}
.course_info_preview #info_cover_img{ position: relative;}
.course_info_preview .info_desc{ position:absolute;bottom:0; left:0; width:100%; color:#fff; max-height:56px; padding:4px 8px; overflow:hidden; line-height:22px; font-size:14px; background:rgba(0,0,0,.6); filter:progid:DXImageTransform.Microsoft.gradient(GradientType=0,startColorstr='#99000000',endcolorstr = '#99000000');}
.course_info_preview .mask{ display:none; position: absolute; left:0; top:0; bottom:0; right:0; width:100%; height:100%; font-size:18px; text-align:center; background:rgba(229,229,229,0.85); filter:progid:DXImageTransform.Microsoft.gradient(GradientType=0,startColorstr='#d9e5e5e5',endcolorstr = '#d9e5e5e5');}
.course_info_preview .info_cover:hover .mask{ display:block;}
.course_info_preview .mask_pos{ position:absolute;top:50%; left:50%; width:50px; height:20px; line-height:20px; margin:-10px 0 0 -25px;}
.course_info_preview .mask_pos a{ display:inline-block; vertical-align:middle; color:#999;}
.course_info_preview .mask_pos a:hover{ color:#000}
.course_info_preview .section_list{ margin-top:15px;}
.course_info_preview .section_list ul,
.course_info_preview .section_list li{ list-style:none; padding:0; margin:0}
.course_info_preview .section_list .item{ position:relative; padding:15px; border-top:#ddd 1px solid}
.course_info_preview .section_list .item:hover .mask{ display:block;}
.course_info_preview .section_list .item .mask .edit{ margin-right:10px}
.course_info_preview .section_add{ padding:15px 15px 0}
.course_info_preview .section_add .btn{ display:block; width:100%; border-style:dotted}

.course_editor .course_editor_c2{ overflow:hidden;}

.form-ele-btm{ padding-left:365px; padding-bottom:70px}
.form-ele-btm input{ width:106px; margin-right:20px}